<template>
	<div class="storage_content">
		<v-typesearch v-on:search="search" v-on:getMaterialList="getMaterialList" ></v-typesearch>
		<div>
			<template>
			<el-table
				:header-cell-style="{background:'#DFEBF9',color:'#606266'}"
				:loading="loading"
				:data="matlist"
				:empty-text="$t('common.nodata')"
				border>									
				<el-table-column
					:label="$t('common.No')"
		      type="index"
		      width="50">
		    </el-table-column>
				<el-table-column
					prop="shelf_id"
				  :label="$t('storage.shelf_id')">						
				</el-table-column>				
				<el-table-column
					prop="part_num"
				  :label="$t('storage.part_num')">						
				</el-table-column>
				<el-table-column
					prop="label_code"
				  :label="$t('storage.label_code')">						
				</el-table-column>
				<el-table-column
					prop="warehouse_name"
				  :label="$t('storage.warehouse_name')">						
				</el-table-column>
				<el-table-column
					prop="customer"
				  :label="$t('storage.customer_name')">					
				</el-table-column>
				<el-table-column
					prop="supplier_name"
				  :label="$t('storage.supplier_name')">						
				</el-table-column>
				<el-table-column
					:show-overflow-tooltip='true'
					prop="position"
				  :label="$t('storage.position')">						
				</el-table-column>
				<el-table-column
					:show-overflow-tooltip='true'
					prop="description"
				  :label="$t('storage.description')">						
				</el-table-column>
				<el-table-column
					prop="state"
				  	:label="$t('storage.state')">
					<template slot-scope="scope">
						<span v-if="scope.row.state==0" class="take_mat_span">在库</span>
						<span v-if="scope.row.state==1" class="stay_mat_span">待取</span>
					</template> 							
				</el-table-column>			
				<el-table-column
			  	prop=""
			  	:label="$t('common.operation')"
			  	width="98">
			  	<template slot-scope="scope">
			  		<el-button type='primary' size="mini"  @click="takeOut(scope.row)" :disabled="scope.row.state? true:false">{{$t('storage.take_out')}}</el-button>
			  	</template>				 
				</el-table-column>
			</el-table>
		</template>
		<div class="hh">
		<el-dialog
		  :title="title"
		  :visible.sync="dialogVisible"
		  width="30%">
		  <div class="storagedetail_input">
		  	<span>{{$t('storage.quantity')}}:&nbsp;</span>
		  	<span>{{materialNum}}</span>
		  </div>
		  <div class="storagedetail_input">
		  	<span class="storagedetail_title">{{$t('storage.order_code')}}</span>
		  	<el-input v-model="order_id" :placeholder="$t('storage.orderidtips')"></el-input>
		  </div>
		  <div class="storagedetail_input">
		  	<span class="storagedetail_title">{{$t('storage.category_name')}}</span>
			  	<el-select value-key="id" v-model="warehouse_category_id" @change="categoryTypeChange">
			    	<el-option
			      v-for="item in categoryList"
			      :key="item.id"
			      :label="item.category_name"
			      :value="item.id">
			    </el-option>
			  	</el-select>
		  </div>
		  <div class="storagedetail_input">
		  	<span class="storagedetail_title">{{$t('storage.warehouse_name')}}</span>
			  	<el-select value-key="id" v-model="warehouse_name_id">
			    	<el-option
			      v-for="item in warehouseList"
			      :key="item.id"
			      :label="item.warehouse_name"
			      :value="item.id">
			    </el-option>
			  	</el-select>
		  </div>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="dialogVisible = false" size="small">取 消</el-button>
		    <el-button type="primary" @click="sure" size="small">确 定</el-button>
		  </span>
		</el-dialog>
		</div>
		</div>
		<v-pagination :totalNum="totalNum" v-on:listenPagesize="changePagesize" v-on:listenCurrentpage="changeCurrent"></v-pagination>
	</div>
</template>

<script>
import VTypesearch from '../materialcommon/typesearch'
import VPagination from '../../common/pagination'
import {searchWarehouseCategory, searchWarehouseNameByCategory } from '@/request/api/materialshelf'
import {mateList, searchByInfo, outByPosition} from '@/request/api/material'
	export default{
		components:{ VTypesearch,VPagination},
		data() {
			return{
				loading:false,
				totalNum:0,
				matlist:[],
				start:0,
				rows:5,
				title:'',
				dialogVisible:false,
				order_id:'',
				params:'',
				timer:null,
				warehouse_category_id:-1,
				warehouse_name_id:-1,
				categoryList:[{'category_name':'其他','id':-1}],
				warehouseList:[{'warehouse_name':'其他','id':-1}],
				materialNum:''//物料弹窗diaolog数量
			}
		},
		beforeDestroy(){
			if(this.timer) { //如果定时器还在运行 或者直接关闭，不用判断
        clearInterval(this.timer); //关闭
    	}
		},
		mounted() {
			this.getMaterialList()
		},
		methods:{
			// 获取列表
			getMaterialList() {
				mateList({start:this.start,rows:this.rows}).then(res =>{
          if(res.result){
            this.matlist = res.mate_list
            this.totalNum = res.rows
          }else{
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
				})
			},
			changePagesize(size){
				this.rows = size
				this.getMaterialList()
			},
			changeCurrent(page){
				this.start = (page-1)*this.rows
				this.getMaterialList()
			},
			// 查询
			search(searchform) {
				searchform.start = this.start
				searchform.rows = this.rows
				searchByInfo(searchform).then(res =>{
					if (res.result) {
						this.matlist = res.mate_list
            this.totalNum = res.rows
					}else{
						this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
					}
				})
			},
			// 取料
			takeOut(value) {
				searchWarehouseCategory({}).then(res =>{
					if (res.result) {
						this.categoryList = this.categoryList.concat(res.data_list)
					} else {
						this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
					}
				})
				// this.order_id = -1
				// this.warehouse_name_id = -1
				this.dialogVisible = true
				this.materialNum = value.quantity
				this.title = this.$t('storage.part_num')+':'+value.part_num
				let data={
					position: value.position,
					shelf_id: value.shelf_id,
					color: this.$store.state.color,
					order_id:'',
					warehouse_name_id:-1,
					warehouse_category_id:-1
				}
				this.params = data
			},
			categoryTypeChange(){
				this.warehouseList = [{'warehouse_name':'其他','id':-1}]
				this.warehouse_name_id = -1
	    	searchWarehouseNameByCategory({warehouse_category_id:this.warehouse_category_id}).then(res =>{
	    		if (res.result) {
	    			this.warehouseList = this.warehouseList.concat(res.data)
	    		}
	    	})
	    },
			sure(){		
				let	_this = this
				this.params.order_id = this.order_id
				this.params.warehouse_name_id = this.warehouse_name_id
				this.params.warehouse_category_id = this.warehouse_category_id
				outByPosition(this.params).then(res =>{
					if (res.result) {
						this.dialogVisible = false	
						this.timer = window.setTimeout(function(){
						 _this.getMaterialList() }, 1500);
					}else{
						this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
					}
				})
			}
		}
	}
</script>
<style scoped>
.hh .el-dialog__body{
	padding: 40px;
}
.el-select-dropdown__wrap{
	margin-bottom: 0px;
}
.el-input__icon{
	line-height: 30px;
}
.take_mat_span{
	background: #28a745;
	padding:6px 8px;
	border-radius: 30%;
	color: #fff;
}
.stay_mat_span{
	background: #e6a23c;
	padding:6px 8px;
	border-radius: 30%;
	color: #fff;
}
.storagedetail_input{
	text-align: left;
}
.storagedetail_title{
	display: block;
	text-align: left;
	height: 40px;
	line-height: 40px;
}
.storagedetail_input .el-select{
	width:100%;
}
</style>


