<template>
  <div class="typesearch">
    <div class="typecontent">
      <!--     库位 -->
      <div class="operation_select input_wrap">
        <el-select value-key="id" v-model="searchform.warehouse_category_id" @change="categoryTypeChange">
          <el-option v-for="item in categoryTypeList" :key="item.id" :label="item.category_name" :value="item.id">
          </el-option>
        </el-select>
      </div>
      <div class="operation_select input_wrap">
        <el-select value-key="id" v-model="searchform.warehouse_name_id">
          <el-option v-for="item in warehouselist" :key="item.id" :label="item.warehouse_name" :value="item.id">
          </el-option>
        </el-select>
      </div>
      <!-- 物料搜索条件 -->
      <div class="operation_select input_wrap">
        <el-select value-key="type_id" v-model="type_id" @change="typeChange(type_id)" :placeholder="$t('storage.placeholder')">
          <el-option change v-for="item in options" :key="item.type_id" :label="item.name" :value="item.type_id">
          </el-option>
        </el-select>
      </div>
      <div class="input_wrap">
        <!-- 				<span class="input_span">{{$t('storage.supplier_name')}}</span> -->
        <el-input v-model="searchcontent" clearable></el-input>
      </div>
      <div class="input_wrap">
        <el-button type="primary" size="mini" @click="search">{{$t('common.search')}}</el-button>
        <el-button type="primary" plain size="mini" @click="refresh">{{$t('common.refresh')}}</el-button>
      </div>
    </div>
    <div class="lightscomp">
      <v-lights></v-lights>
    </div>
  </div>
</template>
<script>
  import {
    searchWarehouseCategory,
    searchWarehouseNameByCategory
  } from '@/request/api/materialshelf'
  import VLights from './lights'
  import VWarehouseselect from '../../materialshelf/warehouseconfig/warehouseselect.vue'
  export default {
    components: {
      VLights,
      VWarehouseselect
    },
    data() {
      return {
        searchform: {
          part_num: '',
          warehouse_category_id: 0,
          warehouse_name_id: 0,
          description: '',
          shelf_id: '',
          start: 0,
          rows: 5,
        },
        searchcontent: '',
        categoryTypeList: [{
          'category_name': '所有',
          'id': 0
        }],
        warehouselist: [{
          'warehouse_name': '所有',
          'id': 0
        }],
        options: [{
            name: this.$t('storage.shelf_id'),
            type_id: 'shelf_id'
          },
          {
            name: this.$t('storage.description'),
            type_id: 'description'
          },
          {
            name: this.$t('storage.part_num'),
            type_id: 'part_num'
          },
        ],
        type_id: '',
      }
    },
    created() {
      this.getCategoryType()
    },
    methods: {
      search() {
        if (this.type_id == 'shelf_id') {
          this.searchform.shelf_id = this.searchcontent;
        }
        if (this.type_id == 'description') {
          this.searchform.description = this.searchcontent;
        }
        if (this.type_id == 'part_num') {
          this.searchform.part_num = this.searchcontent;
        }
        this.$emit('search', this.searchform)
      },
      refresh() {
        this.$emit('getMaterialList')
        this.searchcontent = ''
        this.type_id = ''
        this.searchform.warehouse_category_id = 0
        this.searchform.warehouse_name_id = 0
        this.searchform.part_num = ''
        this.searchform.description = ''
        this.searchform.shelf_id = ''
        this.searchform.start = 0
        this.searchform.rows = 5
      },
      // 获取仓库类型
      getCategoryType() {
        searchWarehouseCategory({}).then(res => {
          if (res.result) {
            this.categoryTypeList = this.categoryTypeList.concat(res.data_list)
          }
        })
      },
      // 获取库位
      getWarehouseList() {
        this.warehouselist = [{
          'warehouse_name': '所有',
          'id': 0
        }]
        this.searchform.warehouse_name_id = 0
        searchWarehouseNameByCategory({
          warehouse_category_id: this.searchform.warehouse_category_id
        }).then(res => {
          if (res.result) {
            this.warehouselist = this.warehouselist.concat(res.data)
          }
        })
      },
      categoryTypeChange() {
        this.searchform.warehouse_name_id = 0
        this.getWarehouseList()
      },
      changeWarehose(value) {
        this.searchform.storage_location = value
      },
      typeChange(value) {
        this.type_id = value
      }

    }
  }
</script>
<style>
  .typesearch {
    display: flex;
    font-size: 14px;
    margin: 4px 10px;
  }

  .typesearch .el-input__inner {
    width: 160px;
    height: 40px;
  }

  .typesearch .el-input {
    height: 40px;
    width: 160px;
  }

  .typecontent {
    display: flex;
    flex-wrap: wrap;
    flex: 1;
  }

  .lightscomp {
    width: 300px;
  }

  .input_wrap {
    display: flex;
    padding: 6px;
  }

  .input_span {
    display: inline-block;
    width: 92px;
    height: 40px;
    line-height: 40px;
  }

  .input_wrap .el-button {
    padding: 4px 12px;
    height: 40px;
    width: 60px;
  }

  .input_wrap .el-select-dropdown__item {
    font-size: 12px;
  }
</style>



