<!-- 分页 -->
<template>
  <div class="pagination">
    <div class="block">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="1"
        :page-sizes="[5, 10, 20, 50]" :page-size='5' layout="total, sizes, prev, pager, next, jumper" :total='totalNum'>
      </el-pagination>
    </div>
  </div>
</template>
<script>
  export default {
    props: ['totalNum'],
    data() {
      return {

      }
    },
    mounted() {

    },
    methods: {
      handleSizeChange(size) {
        this.$emit('listenPagesize', size)
      },
      handleCurrentChange(page) {
        this.$emit('listenCurrentpage', page)
      }
    }
  }
</script>
<style>
  .pagination .el-pagination {
    text-align: left;
  }
</style>



